import React, { Component } from 'react'
import { Link ,Switch,Route,useParams,useRouteMatch} from 'react-router-dom'
import '../css/ChooseSubject.css'
const ChooseSubject= ()=>{

   return  (<React.Fragment>
                <h1 className="Welcome-Text">Select a Subject for Quiz</h1> 
                <div className="Container">
                  <div>
                   <Link to="/QuizPage/HTML"><button className="startbtn">HTML</button></Link></div> 
                   <div> <Link to="/QuizPage/CSS"><button className="startbtn">CSS</button></Link></div> 
                   <div> <Link to="/QuizPage/JavaScript"><button className="startbtn">JavaScript</button></ Link></div> 
                   <div><Link to="/QuizPage/NodeJS"><button className="startbtn">NodeJS</button></Link></div> 
                   <div> <Link to="/QuizPage/ReactJS"><button className="startbtn">ReactJS</button></Link></div> 
                   <div> <Link to="/QuizPage/Java"><button className="startbtn">Java</button></Link></div> 
                </div>
            </React.Fragment>)
}
export default ChooseSubject;