import React  from "react";
import ReactStoreIndicator from 'react-score-indicator'
function ScorePanel(props){
    return (  
     <React.Fragment>   
        <ReactStoreIndicator
            value={props.Score}
            maxValue={props.length*5}
            fadedOpacity={20}
            lineGap={0}
            lineWidth={10}	
            style={{"margin-top":'30px'}}
        />
        <h1>{props.Score>(props.length*5)/2?"Excellent":props.Score>(props.length*5)/3?"Good":"Need To Improve"}</h1>
        <div>
            <h2>Total Questions : {props.length}</h2>
            <h2>Attempted Questions: {props.attemptedQ}</h2>
            <h2>Correct Ans :{props.Score/5||0}</h2>
        </div>
    </React.Fragment>
    );
}
export default ScorePanel
