import React ,{useState,useEffect} from 'react';
import {Redirect} from 'react-router-dom';
import ReactLoading from 'react-loading';
import '../css/QuizPage.css';
// import Questions from '../components/Questions';
import ScorePanel from './ScorePanel';
import { CountdownCircleTimer } from 'react-countdown-circle-timer'
const QuizPage=(props)=>{
    const [questionNo, setquestionNo] = useState(0);
    const [attemptedQ,setAttemptedQ]= useState(0);
    const [Questions,setQuestions]=useState(null);
    const [Score, setScore] = useState(0);
    const [finalScore,setFinalScore]=useState(false);
    function NextQuestion(){
        if(questionNo>=Questions.length-1){
            setFinalScore(()=>true);
        }
        else{
            setquestionNo((old)=>old+1);  
        }
    }
    function handleClick(event,userans){
        if(userans===Questions[questionNo].correctans){
            setScore((oldScore)=>oldScore+5);
        }
        else {
        }
        setAttemptedQ(old=>old+1);
        NextQuestion();
    }
    useEffect(()=>{
        let url="/Questions/"+ props.subject;
        fetch(url).then(res=>res.json()).then(data=>{
                setQuestions(data);
        })}  
        
    ,[]);
    return( 
        <React.Fragment>
            <h1 className="Welcome-Text">Welcome to {props.subject.toUpperCase()} Quiz</h1>
            <div className="Container">
                {finalScore ? (
                <React.Fragment>
                   <ScorePanel Score={Score} attemptedQ={attemptedQ} length={Questions.length}/>
                </React.Fragment>
                 ):(
                <React.Fragment>    
                <div className="QuestionInfo">
                   <div>
                       <h2>Question No. {Questions?questionNo+1:0}</h2>
                   </div>
                   <div style={{marginTop:'10px'}}>
                        <CountdownCircleTimer
                            isPlaying={Questions?true:false}
                            duration={50}
                            size={100}
                            colors={[
                            ['#004777', 0.33],
                            ['#F7B801', 0.33],
                            ['#A30000', 0.33],
                            ]}
                            >
                            {({remainingTime})=>{
                                if(remainingTime==0){
                                    setFinalScore(()=>true);
                                }
                                return (<div>
                                            <h1>{remainingTime}</h1>
                                        </div>)
                            }}
                        </CountdownCircleTimer>
                   </div>
                   <div>
                       <h2>Total Question {Questions?Questions.length:0}</h2>
                   </div>
               </div>
               {Questions?(
               <React.Fragment>
               <div className="Question-Container">
                    <h2>{Questions[questionNo].question}</h2> 
               </div>
               <div className="OptionList">
                    <div>
                        <button onClick={(event)=>handleClick(event,1)}> {Questions[questionNo].option1}</button>
                        <button onClick={(event)=>handleClick(event,2)}> {Questions[questionNo].option2}</button>
                        <button onClick={(event)=>handleClick(event,3)}> {Questions[questionNo].option3}</button>
                        <button onClick={(event)=>handleClick(event,4)}> {Questions[questionNo].option4}</button>
                    </div>
               </div>
               <div className="NextQuestionDiv">
                     <div></div>
                    <button onClick={(event)=>NextQuestion(event)}> Next</button>
               </div>
               </React.Fragment>
               ):(
               <div className="Loader">
                   <ReactLoading type={'bubbles'} color={'#000'} height={250} width={250} />
                   <h1>Work In Progress</h1>
                </div>
                )}
               </React.Fragment>
               )}
            </div>
        </React.Fragment>

    )
}
export default QuizPage;