import './App.css';
import Home from'./components/Home';
import ChooseSubject from './components/ChooseSubject';
import QuizPage from './components/QuizPage'
import {BrowserRouter as Router, Switch, Route } from 'react-router-dom'
function App() {
  return (
    <div className="App">
      <Router>
          <Switch>
              <Route path="/" exact component={Home} />
              <Route path="/ChooseSubject" component={ChooseSubject} />
              <Route exact path="/QuizPage/JavaScript" >
                <QuizPage subject="JavaScript"/>
              </Route> 
              <Route exact path="/QuizPage/Java" >
                <QuizPage subject="Java"/>
              </Route>
              <Route exact path="/QuizPage/NodeJS" >
                <QuizPage subject="NodeJS"/>
              </Route> 
              <Route exact path="/QuizPage/HTML" >
                <QuizPage subject="HTML"/> 
              </Route> 
              <Route exact path="/QuizPage/CSS" >
                <QuizPage subject="CSS"/>
              </Route> 
              <Route exact path="/QuizPage/ReactJS" >
                <QuizPage subject="ReactJS"/>
              </Route> 
          </Switch>
      </Router>
    </div>

  );
}

export default App;
